import musichead from '../../components/musichead/musichead.vue'
import '../../common/iconfont.css'
import { topList } from '../../common/api.js'
export default {
	data() {
		return {
			topList : []
		}
	},
	components: {
		musichead
	},
	onLoad() {
		topList().then((res)=>{
			if(res.length){
				this.topList = res;
			}
		});
	},
	methods: {
		handleToList(listId){
			uni.navigateTo({
				url: '/pages/list/list?listId=' + listId
			});
		}
	}
}
